@-webkit-keyframes fadeInLeftBig {
    from {
        transform: translate3d(-100%,0,0);

        opacity: 0;
    }
    100% {
        transform: none;

        opacity: 1;
    }
}
@keyframes fadeInLeftBig {
    from {
        transform: translate3d(-100%,0,0);

        opacity: 0;
    }
    100% {
        transform: none;

        opacity: 1;
    }
}

.fadeInLeftBig {
    animation-name: fadeInLeftBig;
}

.sidebar {
    position: fixed;
    z-index: 40;

    display: inline-block;

    width: 11.12%;
    height: 100vh;
    padding-top: 40px;

    vertical-align: middle;

    background-color: #fff;

    @media (max-width: 1366px) {
        width: 16.6vw;
    }

    @media (max-width: 999px) {
        z-index: 70;
        top: 0;

        width: 100vw;
        height: 70px;
        padding-top: 0;

        background-color: #fff;
    }
}

.menu {
    position: absolute;
    top: 50%;
    left: 0;

    width: 100%;

    transform: translateY(-50%);

    @media (max-width: 999px) {
        position: relative;

        display: inline-block;

        width: calc(100% - 70.5px);
        margin-left: 0;

        text-align: right;

        background-color: #fff;
    }
}

.submenu {
    width: 100%;
    padding-left: 21%;

    list-style: none;

    @media (max-width: 1599px) {
        padding-left: 12%;
    }

    @media (max-width: 1199px) {
        padding-left: 10%;
    }

    @media (max-width: 999px) {
        position: fixed;
        top: 70px;
        // right: -1%;

        width: 101%;
        height: 100vh;
        margin-top: 0;
        padding-top: 15%;

        transition: transform .6s cubic-bezier(.77,0,.175,1);
        text-align: left;

        background-color: #232855;

        &:not(:global(.active)) {
            transform: translateX(100%);
        }
    }
    @media (max-width: 999px) {
        &::before {
            position: fixed;
            z-index: -1;
            top: 0;
            left: 0;

            width: 100vw;
            height: 100vh;

            content: "";
            transition: opacity .6s cubic-bezier(.77,0,.175,1),transform 0s ease-out .6s;
            transform: translateX(0);

            opacity: 0;
            background-color: #232855;
        }

        &:global(.active)::before {
            top: 70px;

            transition: opacity .6s cubic-bezier(.77,0,.175,1),top 0s ease-out .6s;
            transform: translateX(-100%);

            opacity: .5;
        }
    }

    a {
        font-size: 16px;

        position: relative;

        display: inline-block;

        padding-top: 15px;
        padding-bottom: 4px;

        text-decoration: none;

        color: #252525;

        @media (max-width: 1599px) {
            font-size: 15px;
        }

        @media (max-width: 1199px) {
            font-size: 14px;
        }

        @media (max-width: 999px) {
            font-size: 20px;

            color: #f7d781;
        }

        &:after {
            position: absolute;
            bottom: 0;
            left: 0;

            width: 0;
            height: 2px;

            content: "";
            transition: width .2s cubic-bezier(.77,0,.175,1);

            background-color: #f67280;
        }

        &:hover::after {
            width: 100%;
        }
    }
}

.menu_item {
    font-weight: 400;

    margin: 0;

    &:global(.active) {
        font-weight: 700;
    }
}

.button {
    font-size: 15px;
    font-weight: 700;

    position: absolute;
    bottom: 40px;
    left: 0;

    width: 100%;
    padding: 19.5px 0;

    text-align: center;
    text-decoration: none;

    color: #252525;

    &::before {
        position: absolute;
        z-index: -2;
        top: 0;
        left: 0;

        width: 100%;
        height: 100%;

        content: "";

        background-color: #f7d781;
    }

    @media (max-width: 999px) {
        display: none;
    }

    &::after {
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;

        width: 0;
        height: 100%;

        content: "";
        transition: width .3s cubic-bezier(.645,.045,.355,1);

        background-color: #f67280;
    }

    &:hover::after {
        width: 100%;
    }

    &:hover {
        color: #fff;
    }
}

.mobile_menu {
    font-family: aktiv-grotesk,sans-serif;
    font-size: 14px;
    font-weight: 700;

    position: relative;

    display: none;

    margin-right: -4px;
    padding: 1.6875rem .9375rem;

    transition: color .2s cubic-bezier(.645,.045,.355,1);
    text-decoration: none;
    letter-spacing: 3px;
    text-transform: uppercase;

    color: #394459;
    background: 0 0;

    @media (max-width: 999px) {
        display: inline-block;
    }

    &::before {
        position: absolute;
        z-index: -1;
        bottom: 0;
        left: 0;

        width: 101%;
        height: 0;

        content: "";
        transition: height .2s cubic-bezier(.645,.045,.355,1) 0s;

        background-color: #232855;
    }

    &:global(.active) {
        transition: color .2s cubic-bezier(.645,.045,.355,1) .6s;

        color: #f7d781;
    }

    &:global(.active)::before {
        height: 110%;

        transition: height .2s cubic-bezier(.645,.045,.355,1) .6s;
    }
}

.menu_menubar {
    font-family: aktiv-grotesk,sans-serif;

    position: relative;
    z-index: 2;

    display: inline-block;
    // width: 20px;

    height: 2px;
    margin-left: 10px;
    padding: 0 10px;

    transition: transform .2s cubic-bezier(.645,.045,.355,1) 0s,background-color .2s cubic-bezier(.645,.045,.355,1) 0s;
    transform: rotate(0);
    vertical-align: middle;

    background-color: #394459;

    &:global(.active) {
        transition: transform .2s cubic-bezier(.645,.045,.355,1) .3s,background-color .2s cubic-bezier(.645,.045,.355,1) .3s;
        transform: rotate(45deg);

        background-color: #0000;
    }

    &::before {
        position: absolute;
        bottom: -7px;
        left: 0;

        width: 100%;
        height: 2px;

        content: "";
        transition: bottom .2s cubic-bezier(.645,.045,.355,1) .2s,background-color .2s cubic-bezier(.645,.045,.355,1);

        background-color: #394459;
    }

    &:global(.active)::before {
        bottom: 0;

        transition: bottom .2s cubic-bezier(.645,.045,.355,1),background-color .2s cubic-bezier(.645,.045,.355,1) .3s;

        background-color: #d2d2d2;
    }

    &::after {
        position: absolute;
        top: -7px;
        left: 0;

        width: 100%;
        height: 2px;

        content: "";
        transition: top .2s cubic-bezier(.645,.045,.355,1) .2s,transform .2s cubic-bezier(.645,.045,.355,1),background-color .2s cubic-bezier(.645,.045,.355,1);
        transform: rotate(0);

        background-color: #394459;
    }

    &:global(.active)::after {
        top: 0;

        transition: top .2s cubic-bezier(.645,.045,.355,1),transform .2s cubic-bezier(.645,.045,.355,1) .2s,background-color .2s cubic-bezier(.645,.045,.355,1) .3s;
        transform: rotate(90deg);

        background-color: #d2d2d2;
    }
}

.logo {
    margin: 0;

    @media (max-width: 999px) {
        display: inline-block;

        width: 70px;
        height: 70px;

        vertical-align: top;

        background-color: #f7d781;
    }

    a {
        width: 100%;
    }
}

.logo-mobile {
    display: none;
    @media (max-width: 999px) {
        display: inline-block;
    }
}
