.wrap {
    overflow: hidden;

    width: 100%;
    min-height: 100vh;
    margin-left: 11.11vw;

    @media (max-width: 1366px) {
        margin-left: 16.6vw;
    }

    @media screen and  (max-width: 999px) {
        margin-left: 0;
    }
}

.news_article {
    position: relative;

    display: inline-block;
    overflow: hidden;

    width: 77.77vw;
    height: calc(100vh - 60px);
    margin-left: 11.11vw;

    vertical-align: middle;

    @media (max-width: 1366px) {
        margin-left: 8.4vw;
    }

    @media (max-width: 999px) {
        z-index: 9;

        overflow-y: visible;

        width: 100%;
        height: auto;
        min-height: 80vh;
        margin-left: 0;
    }
}

.article_image {
    position: relative;
    z-index: 15;

    float: right;
    overflow: hidden;

    width: 33.4vw;
    height: 100vh;
    margin: 0;

    vertical-align: middle;

    background-size: cover;

    img {
        position: relative;
        z-index: 5;

        max-width: none;
    }

    @media (max-width: 999px) {
        position: relative;
        z-index: 0;
        top: 0;

        float: none;

        width: 100%;
        height: 50vh;

        img {
            position: absolute!important;
            z-index: 5;
            top: 0;
            right: 0;

            width: 100%;
            height: auto;
        }
    }
}

.article_title {
    font-size: 5em;
    font-weight: 700;

    position: absolute;
    z-index: 40;
    top: 20%;

    display: inline-block;
    overflow: hidden;

    width: 44.44vw;
    margin-left: -7px;

    @media (max-width: 999px) {
        font-size: 40px;

        position: relative;
        z-index: 10;
        top: auto;
        bottom: 0;

        width: auto;
        margin-bottom: 0;
        padding: 30px;
    }

    a {
        font-weight: 700;

        position: relative;

        display: inline-block;

        cursor: default;
        transition: color .2s cubic-bezier(.25,.46,.45,.94);
        text-decoration: none;

        color: #fecea8;
        @media (min-width: 1000px) {
            padding-bottom: 20px;
        }

        @media (max-width: 999px) {
            padding-bottom: 10px;
        }

        &::before {
            position: absolute;
            z-index: 5;
            bottom: 0;
            left: 5px;

            width: calc(100% - 8px);
            height: 8px;

            content: "";

            background-color: #fecea8;

            @media (min-width: 1600px) {
                height: 10px;
            }
            @media (max-width: 1599px) {
                height: 7px;
            }
            @media (max-width: 1199px) {
                height: 5px;
            }
            @media (max-width: 999px) {
                height: 3px;
            }
        }
    }

    @media (max-width: 1599px) {
        font-size: 4.5em;
    }

    @media (max-width: 1199px) {
        font-size: 3em;
    }

    @media (max-width: 999px) {
        font-size: 40px;

        position: relative;
        z-index: 10;
        top: auto;
        bottom: 0;

        width: auto;
        margin-bottom: 0;
        padding: 30px;
    }
}

.article_item {
    position: absolute;
    z-index: 1;

    overflow: hidden;

    width: 33.33vw;
    height: calc(100vh - 82px);

    @media (max-width: 1366px) {
        width: 41.6vw;
    }

    @media (max-width: 999px) {
        position: relative;

        width: 100%;
        height: auto;
        // min-height: 80vh;
    }
}

.article_info {
    font-size: 12px;
    // transition: transform 1s cubic-bezier(.25,.46,.45,.94),opacity 1s cubic-bezier(.25,.46,.45,.94);
    // transform: translateX(-150%);

    letter-spacing: 2px;
    // text-transform: uppercase;

    opacity: 1;
    color: #a6a6a6;
}

.article_intro {
    font-size: 32px;
    font-weight: 700;
    line-height: 1.6;

    max-width: 33vw;

    // transition: transform 1s cubic-bezier(.25,.46,.45,.94) .4s,opacity 1s cubic-bezier(.25,.46,.45,.94) .4s;
    // transform: translateX(-150%);

    // opacity: 0;

    a {
        text-decoration: none;

        color: #252525;
    }

    @media (max-width: 1199px) {
        font-size: 24px;
    }

    @media (max-width: 999px) {
        max-width: 100vw;
        margin: 0;
    }
}

.article_more {
    font-size: 16px;
    font-weight: 700;

    position: relative;

    display: inline-block;

    padding-bottom: 5px;
    // transition: transform .6s cubic-bezier(.25,.46,.45,.94) 1s,opacity .6s cubic-bezier(.25,.46,.45,.94) 1s,color .2s cubic-bezier(.25,.46,.45,.94),-webkit-transform .6s cubic-bezier(.25,.46,.45,.94) 1s;
    // transform: translateX(-150%);

    text-decoration: none;
    letter-spacing: 2px;
    text-transform: uppercase;
    // opacity: 0;

    color: #f7d781;

    &::before ,
    &::after {
        position: absolute;
        bottom: 0;
        left: 0;

        height: 2px;

        content: "";
    }

    &::after {
        width: 0;

        transition: width 6s linear .5s;

        background-color: #252525;
    }
    &::before {
        width: 100%;

        background-color: #f7d781;
    }

    .overlay {
        position: absolute;
        top: 0;
        left: 0;

        overflow: hidden;

        width: 0;

        -webkit-transition: width 6s linear .5s;
                transition: width 6s linear .5s;
        white-space: nowrap;

        color: #252525;
        background-color: #fff;
    }
}

.article_content {
    position: absolute;
    z-index: 2;
    top: 45%;
    left: 0;

    display: inline-block;
    visibility: visible;

    width: 33.33vw;
    padding-right: 80px;
    // transform: matrix(1, 0, 0, 1, 0, 0);

    vertical-align: middle;

    opacity: 1;

    @media (max-width: 999px) {
        position: relative;
        z-index: 0;
        top: 0;

        display: block;

        width: calc(100vw - 24px);
        margin: 0;
        padding: 0 12px;

        transform: matrix(1, 0, 0, 1, 0, 0);
        transform: translateY(0);
        vertical-align: middle;
    }
}
