.background {
    position: fixed;
    z-index: 10;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    overflow: hidden;

    width: 101vw;

    pointer-events: none;
}



.line {
    position: relative;
    z-index: 0;

    display: inline-block;
    overflow: hidden;

    width: 11.11vw;
    height: 100vh;

    border-right: 1px solid rgba(0, 0, 0, .08);

    @media (max-width: 1366px) {
        width: 16.66vw;
    }

    @media (max-width: 999px) {
        width: 33.33vw;
    }
}
