html {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

body {
    overflow-x: hidden;
    // overflow-y: auto;
}

body.active {
    overflow: hidden;
}

h1 {
    color: darkorange;
}


::-webkit-scrollbar {
  width: 10px;
  background-color: #ddd
}

@media (max-width: 999px) {
  ::-webkit-scrollbar {
    display: none;
  }
}

::-webkit-scrollbar-thumb {
  background-color: #aaa
}

::-webkit-scrollbar-button {
  display: none
}
